<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-table :data="tableData" style="width: 100%" max-height="450" border >
                <el-table-column type="index" label="" align="center" width="400">
                </el-table-column>
                <el-table-column prop="date" label="" align="center" width="500" :formatter="dateFormat">
                </el-table-column>
                <el-table-column prop="content" label="" align="center">
                </el-table-column>
            </el-table>
            <!---->
             <el-row>
                <el-col :span="24">
                    <div class="pagination">
                        <el-pagination
                            v-if='paginations.total > 0'
                            :page-sizes="paginations.page_sizes"
                            :page-size="paginations.page_size"
                            :layout="paginations.layout"
                            :total="paginations.total"
                            :current-page.sync='paginations.page_index'
                            @current-change='handleCurrentChange'
                            @size-change='handleSizeChange'>
                        </el-pagination>
                    </div>
                </el-col>
            </el-row>

        </div>
        
    </div>
        
</template>

<script>
import scheduleApi from '../api/schedule.js'

export default {
    name:"scheduleList",
    components: {
        
    },
    data () {
        return {
            //
            paginations: {
                page_index: 1, // 
                total: 0, // 
                page_size: 5, // 1
                page_sizes: [5, 10, 15, 20], //
                layout: "total, sizes, prev, pager, next, jumper" // 
            },
            
           tableData:[],
           allTableData: [],
           
        };
    },
    created () {
        this.getProfile();
    },
    methods: {
        getProfile(){
            scheduleApi.getAllScheduleList().then(res =>{
                if(res.data.flag == true){
                    this.allTableData = res.data.data;
                    // 
                    this.setPaginations();
                }else{
                    this.$message({
                    message: '',
                    type: 'warning'
                    });
                }
            })
        },
        
        handleCurrentChange(page) {
            // 
            let sortnum = this.paginations.page_size * (page - 1);
            let table = this.allTableData.filter((item, index) => {
                return index >= sortnum;
            });
            // 
            this.tableData = table.filter((item, index) => {
                return index < this.paginations.page_size;
            });
            
        },
        handleSizeChange(page_size) {
            // size
            this.paginations.page_index = 1;
            this.paginations.page_size = page_size;
            this.tableData = this.allTableData.filter((item, index) => {
                return index < page_size;
            });
         },
        setPaginations() {
            // 
            this.paginations.total = this.allTableData.length;
            this.paginations.page_index = 1;
            this.paginations.page_size = 5;
            // 
            this.tableData = this.allTableData.filter((item, index) => {
                return index < this.paginations.page_size;
            });
        },
        dateFormat:function(row, column) { 
            var date = row[column.property]; 
            if (date == undefined) { 
            return ""; 
            } 
            return this.$moment(date).format("YYYYMMDD"); 
        } 
    }
}
</script>


<style scoed>
.fillcontain {
  width: 100%;
  height: 100%;
  padding: 16px;
  box-sizing: border-box;
}
.btnRight {
  float: right;
}
.pagination {
  text-align: right;
  margin-top: 10px;
}
</style>

